*{margin:0;padding:0;box-sizing:border-box}button,input{outline:none}.icon{width:30px;height:100%}.app{height:100vh;width:100%;display:flex;align-items:center;flex-direction:column;justify-content:space-evenly;background:#073b4c;font-family:"Fira Sans",sans-serif;padding:5px}.header{display:flex;flex-flow:column nowrap}.header-title{text-transform:capitalize;color:#f4f4f4;font-size:50px}.form{width:80vw;display:flex;height:60px}.folding-cube{width:20px;height:20px;position:absolute;-webkit-transform:rotateZ(45deg);transform:rotateZ(45deg)}.folding-cube .cube{float:left;width:50%;height:50%;position:relative;-webkit-transform:scale(1.1);transform:scale(1.1)}.folding-cube .cube:before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-color:#06d6a0;-webkit-animation:foldCubeAngle 2.4s infinite linear both;animation:foldCubeAngle 2.4s infinite linear both;-webkit-transform-origin:100% 100%;transform-origin:100% 100%}.folding-cube .cube2{-webkit-transform:scale(1.1) rotateZ(90deg);transform:scale(1.1) rotateZ(90deg)}.folding-cube .cube3{-webkit-transform:scale(1.1) rotateZ(180deg);transform:scale(1.1) rotateZ(180deg)}.folding-cube .cube4{-webkit-transform:scale(1.1) rotateZ(270deg);transform:scale(1.1) rotateZ(270deg)}.folding-cube .cube2:before{-webkit-animation-delay:0.3s;animation-delay:0.3s}.folding-cube .cube3:before{-webkit-animation-delay:0.6s;animation-delay:0.6s}.folding-cube .cube4:before{-webkit-animation-delay:0.9s;animation-delay:0.9s}@-webkit-keyframes foldCubeAngle{0%,10%{-webkit-transform:perspective(140px) rotateX(-180deg);transform:perspective(140px) rotateX(-180deg);opacity:0}25%,75%{-webkit-transform:perspective(140px) rotateX(0deg);transform:perspective(140px) rotateX(0deg);opacity:1}90%,100%{-webkit-transform:perspective(140px) rotateY(180deg);transform:perspective(140px) rotateY(180deg);opacity:0}}@keyframes foldCubeAngle{0%,10%{-webkit-transform:perspective(140px) rotateX(-180deg);transform:perspective(140px) rotateX(-180deg);opacity:0}25%,75%{-webkit-transform:perspective(140px) rotateX(0deg);transform:perspective(140px) rotateX(0deg);opacity:1}90%,100%{-webkit-transform:perspective(140px) rotateY(180deg);transform:perspective(140px) rotateY(180deg);opacity:0}}

.container{background:#073b4c;display:flex;flex-flow:column wrap;height:80vh;width:80vw}@media only screen and (max-device-width: 1024px) and (orientation: portrait) and (-webkit-min-device-pixel-ratio: 2){.container{flex-flow:row wrap}}


.buttons{color:#f4f4f4;font-family:"Fira Sans",sans-serif;font-weight:200}.button{display:flex;align-items:center;justify-content:center;cursor:pointer;background:#f4f4f4;z-index:1;background-size:cover;background-repeat:no-repeat;padding:10px;-webkit-transition:all 150ms cubic-bezier(0.075, 0.82, 0.165, 1);transition:all 150ms cubic-bezier(0.075, 0.82, 0.165, 1)}@media only screen and (max-device-width: 1024px) and (orientation: portrait) and (-webkit-min-device-pixel-ratio: 2){.button{width:30px;height:30px;border-width:2px;padding:2px}}.button:hover{opacity:0.9}.button--add,.button--remove{top:50%;border-radius:100%;border:10px solid;opacity:0.7;width:70px;height:70px;position:absolute}.button--add:active,.button--remove:active{padding:0;border-width:7px}.button--add{background-image:url(/photo-gallery/static/media/plus.94c9238b.svg);color:#06d6a0;border-color:#06d6a0;left:90%}.button--remove{background-image:url(/photo-gallery/static/media/minus.2df31d1e.svg);color:#ef476f;border-color:#ef476f;right:90%}.button--submit{background-color:#118ab2;border:10px solid #118ab2;width:70px;opacity:0.9;fill:#073b4c;padding:0}.button--submit:hover{opacity:1;box-shadow:0 0 5px #118ab2}.button--submit:active{background:#139cc9;border:0}.button--submit:active svg{-webkit-transform:scale(1.1);transform:scale(1.1)}.button--focus{opacity:1;opacity:initial}.button--disabled{cursor:not-allowed}.button--disabled:hover{opacity:0.2}

.input{padding:15px;width:100%;font-size:16px;font-family:"Fira Sans",sans-serif;border:1px solid #118ab2;opacity:0.9;background-color:#073b4c;color:#f4f4f4;border-left-width:10px;-webkit-transition:background-color 250ms cubic-bezier(0.215, 0.61, 0.355, 1);transition:background-color 250ms cubic-bezier(0.215, 0.61, 0.355, 1)}.input:hover{background:rgba(17,138,178,0.5)}.input::-webkit-input-placeholder{color:#c1c1c1}.input::-moz-placeholder{color:#c1c1c1}.input:-ms-input-placeholder{color:#c1c1c1}.input::-ms-input-placeholder{color:#c1c1c1}.input::placeholder{color:#c1c1c1}.input:focus{background:#118ab2;opacity:1;opacity:initial;outline:none;color:#f4f4f4}.input:focus::-webkit-input-placeholder{color:#dbdbdb}.input:focus::-moz-placeholder{color:#dbdbdb}.input:focus:-ms-input-placeholder{color:#dbdbdb}.input:focus::-ms-input-placeholder{color:#dbdbdb}.input:focus::placeholder{color:#dbdbdb}

